@import './mixins';

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 16px;
  row-gap: 40px;

  @include onTablet {
    grid-template-columns: repeat(12, 1fr);
  }

  @include onDesktop {
    grid-template-columns: repeat(24, 1fr);
  }
}

.catalog__card-container {
  display: flex;
  justify-content: center;

  grid-column: span 2;

  @include onTablet {
    grid-column: span 6;
  }

  @include onDesktop {
    grid-column: span 6;
  }
}
