@import '../../styles/utils/mixins';

.not-found {
  padding: 30px 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 70vh;

  background-image: url(../../assets/notFoundFace.png);
  background-size: 360px;
  background-position: right;
  background-repeat: no-repeat;

  @include onTablet {
    background-size: 600px;
  }

  @include onDesktop {
    background-size: 800px;
    background-position: right;
  }

  &__number {
    // animation: blink-glitch 1s infinite;
    animation: rotate-scale-glitch 1s infinite;
  }

  &__btn {
    display: flex;
    align-items: center;
    gap: 4px;

    border: none;
    background: transparent;
    cursor: pointer;

    &-icon {
      fill: var(--gray-primary-color);
      width: 24px;
      height: 24px;
    }

    &-text {
      font-size: 16px;
      font-weight: 700;
      color: var(--gray-primary-color);
    }
  }
}

@keyframes rotate-scale-glitch {
  0% {
    transform: rotate(0deg) scale(1);
  }
  25% {
    transform: rotate(10deg) scale(1.1);
  }
  50% {
    transform: rotate(-10deg) scale(0.9);
  }
  75% {
    transform: rotate(5deg) scale(1.05);
  }
  100% {
    transform: rotate(0deg) scale(1);
  }
}

@keyframes blink-glitch {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
